<template>
  <div id="chart-expense">
    <chart :options="expensesChart" auto-resize></chart>
  </div>
</template>

<script>
import "echarts";
import { graphic } from "echarts/lib/export";

export default {
  name: "ExpenseCharts",
  computed: {
    expensesChart() {
      return {
        backgroundColor: new graphic.LinearGradient(
          0,
          0,
          0,
          1,
          [
            {
              offset: 0,
              color: "#4CA1AF"
            },
            {
              offset: 1,
              color: "#2C3E50"
            }
          ],
          false
        ),
        title: {
          text: "Expenses",
          textStyle: {
            color: "#fff",
            fontSize: 26
          },
          left: "center",
          top: "7%"
        },
        dataset: {
          source: this.$store.state.expenseList
        },
        grid: {
          top: "15%",
          left: "10%",
          right: "10%",
          bottom: "15%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          boundaryGap: false,
          axisLabel: {
            margin: 30,
            color: "#ffffff63"
          },
          axisLine: {
            show: false
          },
          axisTick: {
            show: true,
            length: 25,
            lineStyle: {
              color: "#ffffff1f"
            }
          },

        },
        yAxis: [
          {
            type: "value",
            position: "right",
            axisLabel: {
              margin: 20,
              color: "#ffffff63"
            },

            axisTick: {
              show: true,
              length: 15,
              lineStyle: {
                color: "#ffffff1f"
              }
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#ffffff1f"
              }
            },
            axisLine: {
              lineStyle: {
                color: "#fff",
                width: 2
              }
            }
          }
        ],
        series: [
          {
            name: "????",
            type: "line",
            smooth: true,
            showAllSymbol: true,
            symbol: "circle",
            symbolSize: 6,
            lineStyle: {
              normal: {
                color: "#fff"
              }
            },
            encode: {
              x: "date",
              y: "expense"
            },
            label: {
              show: true,
              position: "top",
              textStyle: {
                color: "#fff"
              }
            },
            itemStyle: {
              color: "red",
              borderColor: "#fff",
              borderWidth: 3
            },
            tooltip: {
              show: false
            },
            areaStyle: {
              normal: {
                color: new graphic.LinearGradient(
                  0,
                  0,
                  0,
                  1,
                  [
                    {
                      offset: 0,
                      color: "#ffd89b"
                    },
                    {
                      offset: 1,
                      color: "#19547b"
                    }
                  ],
                  false
                )
              }
            }
          }
        ]
      };
    }
  },
  methods: {}
};
</script>

<style scoped>
.echarts {
  width: 100%;
  height: 100%;
}
#chart-expense {
  position: fixed;
  width: 100%;
  height: 100%;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  margin: 0px;
  border-width: 0px;
}
</style>



