<template>
  <div>
    <expense-input
      @addExpense="addExpense">
    </expense-input>
    <expense-item
      v-for="item in expenseList"
      :key="item.id"
      :item="item"
      @deleteExpense="deleteExpense">
    </expense-item>

  </div>
</template>

<script>
  import ExpenseItem from './ExpenseItem'
  import ExpenseInput from './ExpenseInput'
export default {
  name: "ExpenseList",
  components: {
    ExpenseItem,
    ExpenseInput
  },
  computed:{
    expenseList() {
      return this.$store.state.expenseList;
    }
  },
  methods: {
    addExpense: function(payload) {
      this.$store.dispatch('addExpense', payload);
    },
    deleteExpense: function(id) {
      this.$store.dispatch('deleteExpense', id);
    }

  }
}
</script>

<style scoped>

</style>



