import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

export const store = new Vuex.Store({
    state: {
        expenseList: [
            {
                id: 1,
                date: "2019-06-01",
                expense: 393
            },
            {
                id: 2,
                date: "2019-06-02",
                expense: 438
            },
            {
                id: 3,
                date: "2019-06-03",
                expense: 485
            },
            {
                id: 4,
                date: "2019-06-04",
                expense: 631
            },
            {
                id: 5,
                date: "2019-06-05",
                expense: 689
            },
            {
                id: 6,
                date: "2019-06-06",
                expense: 824
            },
            {
                id: 7,
                date: "2019-06-07",
                expense: 987
            },
            {
                id: 8,
                date: "2019-06-08",
                expense: 1000
            },
            {
                id: 9,
                date: "2019-06-09",
                expense: 1100
            },
            {
                id: 10,
                date: "2019-06-10",
                expense: 1200
            }
        ]
    },
    getters: {
        maxExpense: (state) => Math.max.apply(Math, state.expenseList.map(o => o.expense)),
        minExpense: (state) => Math.min.apply(Math, state.expenseList.map(o => o.expense))
    },
    mutations: {
        addExpense: (state, payload) => {
            state.expenseList.push(payload);
        },
        deleteExpense: (state, id) => {
            let index = state.expenseList.map(function (item) {
                return item.id
            }).indexOf(id);

            state.expenseList.splice(index, 1);
        }
    },
    actions: {
        addExpense: (context, payload) => {
            context.commit('addExpense', payload);
        },
        deleteExpense: (context, id) => {
            context.commit('deleteExpense', id);
        }
    }
});


