<template>
  <div id="app" class="ui grid">
    <div class="ui row">
      <div class="twelve wide column">
        <expense-charts></expense-charts>
      </div>

      <div class="three wide column">
        <div class="ui fluid primary button" @click="displayExpenseList = !displayExpenseList">{{ displayExpenseList? "Hide": "Show"}} Expense List</div>
        <expense-list v-if="displayExpenseList"></expense-list>
      </div>
    </div>
  </div>
</template>

<script>
import ExpenseList from './components/ExpenseList'
import ExpenseCharts from './components/ExpenseCharts'

export default {
  name: 'App',
  components: {
    ExpenseList,
    ExpenseCharts
  },
  data() {
    return {
      displayExpenseList: true
    }
  }
}
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    color: #2c3e50;
    width: 100vw;
  }


</style>



